import * as XLSX from "xlsx";
import config from "./config.json";

// Define template identifiers at the top of the file
const TEMPLATE_IDENTIFIERS = {
  "1": {
    name: "Physical Product",
    alternateNames: ["Physical Product/Raw Material"], // UI might show different names
    uniqueIdentifiers: {
      requiredHeaders: ["EIN Number", "Type", "Title"], // Must-have headers for this type
      specificFields: ["Unit Of Measure", "MWP"], // Fields specific to this template
    }
  },
  "2": {
    name: "Physical Product Structure",
    alternateNames: [],
    uniqueIdentifiers: {
      requiredHeaders: ["Level", "Find Number"], // Unique to structure template
      specificFields: ["Release Phase", "Change Controlled"]
    }
  },
  "3": {
    name: "Document",
    alternateNames: [],
    uniqueIdentifiers: {
      requiredHeaders: ["Document Type", "Document Name"],
      specificFields: ["PDF File Required", "Agency Approval Review Required"]
    }
  },
  "4": {
    name: "Physical Product-Document",
    alternateNames: [],
    uniqueIdentifiers: {
      requiredHeaders: ["Part Type", "Document Type"],
      specificFields: ["Relationship Type", "New/Existing Document"]
    }
  }
};

const validateFile = (file, collabSpaceTitles, selectedOperation) => {
  return new Promise((resolve, reject) => {
    console.log("Starting file validation for:", file.name, selectedOperation);
    console.log("Selected Operation:", selectedOperation);
    console.log("File Name:", file.name);
    console.log(
      "[validateFile.js] Required Collaborative Space:",
      collabSpaceTitles
    );

    const operationNames = {
      1: "Physical Product",
      2: "Physical Product Structure",
      3: "Document",
      4: "Physical Product-Document",
    };

    if (!selectedOperation) {
      reject({
        errors: ["Please select an operation first."],
        headers: [],
      });
      return;
    }

    // üîç Ensure collabSpaceTitles is an array
    if (!Array.isArray(collabSpaceTitles)) {
      console.error(
        "[validateFile.js] ‚ùå collabSpaceTitles is not an array!",
        collabSpaceTitles
      );
      reject({
        errors: ["Internal error: Invalid Collaborative Space list."],
        headers: [],
      });
      return;
    }

    console.log(
      "[validateFile.js] Required Collaborative Spaces:",
      collabSpaceTitles
    );

    const reader = new FileReader();
    reader.readAsBinaryString(file);

    reader.onload = (e) => {
      try {
        console.log("File successfully read. Parsing...");

        const workbook = XLSX.read(e.target.result, { type: "binary" });
        const sheetName = workbook.SheetNames[0];
        console.log("Detected sheet name:", sheetName);

        const worksheet = workbook.Sheets[sheetName];

        const headers =
          XLSX.utils.sheet_to_json(worksheet, { header: 1 })[0] || [];
        console.log("Extracted headers:", headers);

        if (!headers || headers.length === 0) {
          console.error("‚ùå No headers found in the file.");
          reject({
            errors: ["The uploaded file has no content or is empty."],
            headers: [],
          });
          return;
        }

        // ‚úÖ Convert worksheet into JSON (with empty cell values captured)
        const sheetData = XLSX.utils.sheet_to_json(worksheet, { defval: "" });
        console.log("Extracted sheet data:", sheetData);

        if (sheetData.length === 0) {
          console.error("‚ùå No row data found. Only headers exist.");
          reject({
            errors: [
              "The uploaded file contains only headers but no data. Please add at least one row.",
            ],
            headers,
          });
          return;
        }

        // ‚úÖ Extract headers from the first data row
        const fileHeaders = Object.keys(sheetData[0]);
        console.log("Parsed file headers:", fileHeaders);

        // Get template info for selected operation
        const templateInfo = TEMPLATE_IDENTIFIERS[selectedOperation];
        if (!templateInfo) {
          throw new Error("Invalid operation selected");
        }

        console.log("Validating template:", {
          selectedOperation,
          templateName: templateInfo.name,
          fileHeaders
        });

        // Check unique identifiers
        const { requiredHeaders, specificFields } = templateInfo.uniqueIdentifiers;
        
        // Check if all required headers are present
        const missingRequired = requiredHeaders.filter(
          header => !fileHeaders.includes(header)
        );

        // Check if template-specific fields are present
        const hasSpecificFields = specificFields.some(
          field => fileHeaders.includes(field)
        );

        // Multiple validation checks
        if (missingRequired.length > 0 || !hasSpecificFields) {
          console.error("Template validation failed:", {
            missingRequired,
            hasSpecificFields,
            templateName: templateInfo.name
          });

          reject({
            errors: [
              `The uploaded template does not match ${templateInfo.name} operation.`,
              missingRequired.length > 0 
                ? `Missing required headers: ${missingRequired.join(", ")}`
                : "Template is missing specific required fields."
            ],
            headers: fileHeaders,
            isTemplateMismatch: true
          });
          return;
        }

        // Continue with existing validation...

        // Get expected headers for selected operation
        const expectedHeadersForOperation =
          config.MassUpload[operationNames[selectedOperation]]?.fileHeaders;
        console.log(
          "Expected headers for selected operation:",
          expectedHeadersForOperation
        );

        if (!expectedHeadersForOperation) {
          console.error(
            "‚ùå No template configuration found for selected operation"
          );
          reject({
            errors: ["Invalid operation selected."],
            headers: [],
            isTemplateMismatch: true,
          });
          return;
        }

        // Check if headers match the selected operation's template
        const headersMismatch = !expectedHeadersForOperation.every((header) =>
          fileHeaders.includes(header)
        );

        if (headersMismatch) {
          console.error("‚ùå Template does not match selected operation");
          reject({
            errors: [
              `The uploaded template does not match the selected operation: ${operationNames[selectedOperation]}`,
            ],
            headers: fileHeaders,
            isTemplateMismatch: true,
          });
          return;
        }

        // üîç **Step 1: Find Matching Operation Based on Headers**
        let matchedOperation = null;
        for (const [key, value] of Object.entries(config.MassUpload)) {
          if (value.fileHeaders.every((header) => fileHeaders.includes(header))) {
            matchedOperation = key;
            console.log("Matched operation:", matchedOperation);
            break;
          }
        }
        if (!matchedOperation) {
          console.error("‚ùå No matching operation found for given headers.");
          reject({
            errors: ["File headers do not match any expected template."],
            headers,
          });
          return;
        }

        // ‚úÖ **Step 2: Validate Headers**
        const expectedHeaders = config.MassUpload[matchedOperation].fileHeaders;
        console.log("Expected Headers:", expectedHeaders);
        const mandatoryAttributes =
          config.MassUpload[matchedOperation].mandatoryAttributes;
        console.log("Mandatory Attributes:", mandatoryAttributes);

        // üîç **Step 3: Check for Missing Headers**
        const missingHeaders = expectedHeaders.filter(
          (header) => !fileHeaders.includes(header)
        );
        if (missingHeaders.length > 0) {
          console.error("‚ùå Missing headers:", missingHeaders);
          reject({
            errors: [`Missing required headers: ${missingHeaders.join(", ")}`],
            headers,
            mandatoryAttributes,
          });
          return;
        }

        // üîç **Step 4: Validate Mandatory Fields**
        const validationErrors = [];
        sheetData.forEach((row, index) => {
          mandatoryAttributes.forEach((field) => {
            if (!row[field] || row[field].toString().trim() === "") {
              validationErrors.push(
                `Row ${index + 2}: "${field}" is required but is empty`
              );
            }
          });

          // üîç **Step 5: Validate "Collaborative Space" Field**//

          const userCollabSpace = row["Collaborative Space"]
            ? row["Collaborative Space"].trim()
            : "";
          console.log(
            `[validateFile.js] Row ${index + 2}: User's Collaborative Space:`,
            userCollabSpace
          );

          if (!userCollabSpace) {
            validationErrors.push(
              `Row ${index + 2}: "Collaborative Space"  is Missing or Empty`
            );
          } else if (!collabSpaceTitles.includes(userCollabSpace)) {
            validationErrors.push(
              `Row ${
                index + 2
              }: You are not allowed to create in  "Collaborative Space -" (${userCollabSpace}) `
            );
          }
        });

        // Compare the value in "Collaborative Space" with the provided collabSpaceTitle

        // üî¥ **If there are validation errors, reject but return headers**
        if (validationErrors.length > 0) {
          console.log("Validation errors with mandatory attributes:", {
            errors: validationErrors,
            headers,
            mandatoryAttributes, // Make sure we're including this
          });
          reject({
            errors: validationErrors,
            headers,
            mandatoryAttributes, // Add this line
          });
          return;
        }

        // ‚úÖ **Final Step: Resolve with Headers and Sheet Data**
        console.log("‚úÖ File validation successful. No errors found.");
        resolve({
          headers,
          sheetData,
          validationErrors: [],
          mandatoryAttributes, // Make sure we're returning mandatoryAttributes
        });
      } catch (error) {
        console.error("Error during template validation:", error);
        reject({
          errors: [error.message],
          headers: [],
          isTemplateMismatch: true
        });
      }
    };

    // ‚ùå **Handle File Read Errors**

    reader.onerror = () => {
      console.error("‚ùå Error reading the file.");
      reject({ errors: ["File reading failed."], headers: [] });
    };
  });
};

export default validateFile;
