import React, { useState } from "react";
import { FileUploader } from "react-drag-drop-files";
import "./FileUploader.css";
import useToast from "../../hooks/useToast";

const FileUpload = ({
  fileTypes = ["JPEG", "PNG", "GIF", "XLSX", "CSV"],
  multiple = true,
  onUpload,
  onReset,
  disabled = false,
  message = "Drag & Drop your files here or Click to browse",
}) => {
  const [files, setFiles] = useState([]);
  const { showErrorToast } = useToast();

  const handleChange = (selectedFiles) => {
    const fileArray = multiple ? [...selectedFiles] : [selectedFiles];
    setFiles(fileArray);

    // Callback function to send files to parent component
    if (onUpload) {
      onUpload(fileArray);
    }
  };

  const handleDragOver = (event) => {
    if (disabled) {
      event.preventDefault();
      event.stopPropagation();
      showErrorToast("Please choose operation from the drop down first");
    }
  };

  const handleClearFiles = () => {
    setFiles([]);
    if (onUpload) {
      onUpload([]);
    }
    // Call the reset function to clear all states
    if (onReset) {
      onReset();
    }
  };

  return (
    <div
      className={`file-upload-container ${disabled ? "disabled" : ""}`}
      onDragOver={handleDragOver}
    >
      <h2>
        {disabled ? "Please select an operation first" : "Drag & Drop Files"}
      </h2>
      <div className="upload-area">
        <FileUploader
          multiple={multiple}
          handleChange={handleChange}
          name="file"
          types={fileTypes}
          disabled={disabled}
          hoverTitle={disabled ? "" : "Drop here"}
        />
        <div className="file-list">
          {files.length > 0 ? (
            files.map((file, index) => (
              <div
                className="mb-4"
                key={index}
                style={{ display: "flex", alignItems: "center" }}
              >
                <p className="me-2 mb-0">üìÅ {file.name}</p>
                <button
                  onClick={handleClearFiles}
                  style={{
                    background: "none",
                    border: "none",
                    cursor: disabled ? "not-allowed" : "pointer",
                    color: "red",
                    opacity: disabled ? 0.5 : 1,
                  }}
                  disabled={disabled}
                >
                  X
                </button>
              </div>
            ))
          ) : (
            <p>{message}</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default FileUpload;
